import axios from "../plugins/axios";
import * as types from './types'
export default {
    [types.ADD_ITEM]:({commit,state},payload)=>{ // state是复制的一份，修改不影响真实state

        axios({
            url:"/api/buycarc",
            params:{mid:payload.mid}
        }).then(
            res=>commit("ADD_ITEM",res.data.data)
        )	
        
    },
    
    changeItem:({commit,state},payload)=>{
        // [...state.buycar]
        // {....state}
        let num = 0;
        let list = [];
        let mid =  localStorage.getItem("mid")
        state.buycar.forEach((item,index)=>{
          if(item._id==payload._id){
            item.num = (item.num)*1+payload.num;
            num = item.num ;
          }
        });
        //如果是对象，需要复制一份，不然state.buycar指向的是复制后state修改之前的引用

        axios({
            url:"/api/buycarg",
            params:{mid:mid,sid:payload.sid,dataName:payload.dataName,num:num }
        }).then(
            // res=>list = res.data
            res=>console.log("mid",mid,"sid",payload.sid,"dataName",payload.dataName,"num",num)
            
        )
        commit(types.CHANGE_ITEM,[...state.buycar])
        // commit(types.CHANGE_ITEM,state)  ×
      },
    removeItem:({commit,state},payload)=>{
        let mid =  localStorage.getItem("mid")
        state.buycar.forEach((item,index)=>{
          if(item._id==payload._id){
            state.buycar.splice(index,1);
          }
        });
        axios({
            url:"/api/buycars",
            params:{mid:mid,sid:payload.sid}
        }).then(
            // res=>list = res.data
            res=>console.log("mid",mid,"sid",payload.sid)
            
        )

        commit(types.REMOVE_ITEM,[...state.buycar]);
        // commit(types.REMOVE_ITEM,state.buycar);
      },
    clearBuycar:({commit,state})=>{
        commit(types.CLEAR_BUYCAR,[]);
      }

}