import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);

// import Column from "../pages/Column";
  import Home from "../pages/home/Home";
  import Car from "../pages/car/Car";
  import ClassCom from "../pages/classcom/ClassCom";
  import Login from "../pages/login/Login";
  import ProductsClass from "../pages/productsclass/ProductsClass";
  import Reg from "../pages/reg/Reg";
  import UiMe from "../pages/uime/UiMe";
  import UiProduct from "../pages/uiproduct/UiProduct";
  import Err from "../components/Error";
  import Order from "../pages/order/Order";
  import Settlement from "../pages/settlement/Settlement";
  import MyAddres from "../pages/myaddres/MyAddres";
  import MyAddress from "../pages/myaddress/MyAddress";
  import MyEAddress from "../pages/myeaddress/MyEAddress";
  import MyFind from "../pages/myfind/MyFind";
let routes = [
    {path:"/home",component:Home},
    {path:"/car",component:Car},
    {path:"/classcom",component:ClassCom},
    {path:"/login",component:Login},
    {path:"/productsclass/:id",component:ProductsClass},
    {path:"/reg",component:Reg},
    {path:"/uime",component:UiMe},
    {path:"/order/:id",component:Order},
    {path:"/myaddres",component:MyAddres},
    {path:"/myaddress",component:MyAddress},
    {path:"/myeaddress",component:MyEAddress},
    {path:"/myfind",component:MyFind},
    {path:"/settlement/:id",component:Settlement},
    {path:"/uiproduct/:id",component:UiProduct},
    {path:"/",redirect:"/home"},
    {path:"*",component:Err},
];

let router = new VueRouter({
    routes,
    scrollBehavior (to, from, savedPosition) {
      return {x:0,y:0}
    }

  });

  export default router;