<template>
  <div id="app">
        <Header v-show="$root.$data.bNav"></Header>
        <Loading v-show="$root.$data.bLoading"></Loading>
    <transition
      enter-active-class = "animated bounceInLeft delay-1s"
      leave-active-class = "animated bounceOutRight"
    >
        <router-view></router-view>
    </transition>
        <Footer v-show="$root.$data.bFoot"></Footer>
  </div>
</template>

<script>
import $ from "jquery";
import Header from "./components/Header";
import Footer from "./components/Footer";
import Loading from "./components/Loading";
// import { mapState } from 'vuex';
// import { mapMutations } from 'vuex';

export default {
  name: 'app',
    data(){
        return{}
    },
    watch: {
    $route:{
      handler({path}){
        if(/home/.test(path)){
          this.$root.$data.bNav = true;
          this.$root.$data.bFoot = true;
        }
        if(/classcom|uime|productsclass|myfind/.test(path)){
          this.$root.$data.bNav = false;
          this.$root.$data.bFoot = true;
        }
        if(/login|reg|car|uiproduct|order|settlement|myaddres|myeaddress/.test(path)){
          this.$root.$data.bNav = false;
          this.$root.$data.bFoot = false;
        }

      },
      immediate:true
    }
    
  },


  components: {
    Footer,Header,Loading
  },
  mounted(){
    console.log(window)
    $(function () {
            //绑定滚动条事件
            //绑定滚动条事件
            $(window).bind("scroll", function () {
                var sTop = $(window).scrollTop();
                var sTop = parseInt(sTop);
                if (sTop >= 40) {
                    if (!$("#scrollBg").is(":visible")) {
                        try {
                            $("#scrollBg").slideDown();
                        } catch (e) {
                            $("#scrollBg").show();
                        }
                    }
                }
                else {
                    if ($("#scrollBg").is(":visible")) {
                        try {
                            $("#scrollBg").slideUp();
                        } catch (e) {
                            $("#scrollBg").hide();
                        }
                    }
                }
            });
        })
  },
}
</script>

