<script>
export default {
    data(){
        return{
            username:'',
            password:'',
        
        }
    },
    methods: {
        reg(){
        let formData = new FormData();
        formData.append("username",this.username)
        formData.append("password",this.password)
        // formData.append("icon",this.$refs.f1.files[0])
        axios({
          url:"/api/reg",
          method:"post",
          data:formData
        }).then(
          res=> console.log(res)
        )
      }   
    },
}
</script>
