    .reg-l{display: block;border: 0;width: 80%;margin: 0 auto;font:14px/40px "";padding:3px 15px 3px 15px;border:1px solid #f2f2f2;border-radius: 5px;margin-top: 10px;margin-bottom: 10px;}
