<script>
  	import { swiper, swiperSlide } from "vue-awesome-swiper";
  	require("swiper/dist/css/swiper.css");
  	import { mapGetters, mapActions, mapMutations } from 'vuex';
	import {ADD_ITEM} from '../../../store/types.js';
export default {
	data(){
		const that = this;
		return{
			class1:"m-actionsheet",
			mun:1,
			mid:"",
			list:{},
			imgIndex: 1,
			swiperOption: {
        		//是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
        		notNextTick: true,
        		//循环
        		loop: true,
        		//设定初始化时slide的索引
        		initialSlide: 0,
        		//自动播放
        		autoplay: {
          			delay: 1500,
          			stopOnLastSlide: false,
          			/* 触摸滑动后是否继续轮播 */
         	 		disableOnInteraction: false
        		},
       			 //滑动速度
        		speed: 800,
        		//滑动方向
        		direction: "horizontal",
        		//小手掌抓取滑动
        		grabCursor: true,
        		on: {
          			//滑动之后回调函数
          			slideChangeTransitionStart: function() {
            			/* realIndex为滚动到当前的slide索引值 */
            			that.imgIndex= this.realIndex - 1;
          			},
        		},
        		//分页器设置
        		pagination: {
          			el: ".swiper-pagination",
          			clickable: true,
          			type: "bullets"
        		}
			}
		}
	},
	components:{
		  	swiper,
  			swiperSlide
	},
	methods:{
		jia(){
			this.mun++
		},
		jian(){
			this.mun--
		},
		buycar(){
			
			if(this.mid){
				axios({
					url:"/api/buycarg",
					params:{mid:this.mid,sid:this.id,dataName:this.dataName,num:this.mun}
				}).then(
					res=>this.list = res.data
					
				)
			}else{
				alert("您没有登录")
				this.$router.push("/login")
			}
			console.log(this.list)
		}
	},
	props:['detail','id','dataName'],
	mounted(){
		this.mid = localStorage.getItem("mid")
	},
	destroyed(){
		if(this.mid){
			this.$store.dispatch({type:ADD_ITEM,mid:this.mid});
		}else{
			return
		}
		
	}
}
</script>
