 <template>
    <div class="car">
        <header class="aui-header-default aui-header-fixed ">
		<a href="javascript:history.back(-1)" class="aui-header-item">
			<i class="aui-icon aui-icon-back"></i>
		</a>
		<div class="aui-header-center aui-header-center-clear">
			<div class="aui-header-center-logo">
				<div class="">购物车({{zs}})</div>
			</div>
		</div>
		<a href="#" class="aui-header-item-icon"   style="min-width:0">
			<i class="aui-icon aui-icon-member"></i>
		</a>
	</header>
	<section class="aui-car-content">
		<div class="aui-dri"></div>
		<div class="aui-car-box">
			<div v-for="item of buycar" :key="item.id">
				<div class="aui-car-box-name">
					<!-- <input type="checkbox" class="check goods-check shopCheck"> -->
					<h3>
						<a href="javascript:;">{{item.spsj.data.detail.sjname}}</a>
					</h3>
					<div class="aui-car-coupons">
						<a href="javascript:;" @click="removeItem({_id:item._id,sid:item.sid})">删除</a>
					</div>
				</div>
				<div class="aui-car-box-list">
					<ul>
						<li>
							<div class="aui-car-box-list-item">
								<input type="checkbox" class="check goods-check goodsCheck" :value="item._id" v-model="selectArr">
								<div class="aui-car-box-list-img">
									<a href="javascript:;">
										<img :src="baseUrl + '/upload' + item.spsj.data.img">
									</a>
								</div>
								<div class="aui-car-box-list-text">
									<h4>
										<a href="javascript:;">{{item.spsj.data.title}}</a>
									</h4>
									<div class="aui-car-box-list-text-brief">
										<span>颜色:标配版</span>
									</div>
									<div class="aui-car-box-list-text-price">
										<div class="aui-car-box-list-text-pri">
											<b class="price">{{item.spsj.data.PresentP}}</b>
										</div>
										<div class="aui-car-box-list-text-arithmetic">
											<a href="javascript:;" class="minus" @click="changeItem({_id:item._id,sid:item.sid,dataName:item.dataName,num:-1})">-</a>
											<span class="num">{{item.num}}</span>
											<a href="javascript:;" class="plus" @click="changeItem({_id:item._id,sid:item.sid,dataName:item.dataName,num:1})">+</a>
										</div>
									</div>
								</div>

							</div>
							<div class="aui-dri"></div>
						</li>
						
					</ul>
				</div>
			</div>
		</div>

	</section>
	<div class="carqbsc" v-show="buycar.length>0" @click="clearBuycar({qb:buycar.length})">全部清空</div>
	<div class="aui-payment-bar">
		<div class="all-checkbox"><input type="checkbox" class="check goods-check checkbox" id="AllCheck" @click="selectAll">全选</div>
		<div class="shop-total">
			<strong>合计：<i class="total" id="AllTotal">￥{{zj}}</i></strong>
		</div>
		<a href="javascript:;" class="settlement" @click="qjs">去结算</a>
		<!-- <router-link tag="a" class="settlement" to="/order">去结算</router-link> -->
	</div>
    </div>
</template>
<script>
import {mapGetters,mapActions} from 'vuex'
export default {
	data(){
		return {
			mid:"",
			zs:0,
			zj:0,
			selectArr:[]
		}
	},
	created(){
			this.mid =  localStorage.getItem("mid")
			// console.log(this.mid)
			setInterval(()=>{
				var mzs = 0;
				var mzj = 0;
				for(let a = 0; a<this.buycar.length;a++){
					mzs += (this.buycar[a].num)*1;
					var j = this.buycar[a].spsj.data.PresentP
					// mzj += ((`${this.buycar[a].spsj.data.PresentP}`).replace(/￥/,""))*((this.buycar[a].num)*1)
					// console.log(j.substring(1,j.length))
					mzj += j.substring(1,j.length)*((this.buycar[a].num)*1)
				}
				this.zs = mzs;
				this.zj = mzj;
			},1000)
			
	},
	computed:mapGetters([
    	'buycar'
  	]),
	// methods:mapActions([
	// 'changeItem','clearBuycar','removeItem',
	
	// ]),
	methods: {
		...mapActions([
		'changeItem','clearBuycar','removeItem'
		]),
		selectAll(event) {
                var _this = this;
                // console.log(event.currentTarget)
                if(!event.currentTarget.checked) {
                    this.selectArr = [];
                } else { //实现全选
                    _this.selectArr = [];
                    _this.buycar.forEach(function(item,index) {
						_this.selectArr.push(item._id);
                    });
                }
			},
		qjs(){
			console.log(this.selectArr)
			var id =  "";
			for(var a = 0;a<this.selectArr.length;a++){
				id = id + this.selectArr[a] + ",";
			}
			console.log(id)
			this.$router.push(`/order/id:${id}`)
			},

	},  
	mounted(){

			
			// var mzs = 0;
			// var mzj = 0;
			// for(let a = 0; a<this.buycar.length;a++){
			// 	mzs += (this.buycar[a].num)*1;
			// 	var j = this.buycar[a].spsj.data.PresentP
			// 	// mzj += ((`${this.buycar[a].spsj.data.PresentP}`).replace(/￥/,""))*((this.buycar[a].num)*1)
			// 	console.log(j.substring(1,j.length))
			// 	mzj += j.substring(1,j.length)*((this.buycar[a].num)*1)
			// }
			// this.zs = mzs;
			// this.zj = mzj;

		
	}

}
</script>
<style>
	.carqbsc{width:25%;height: 30px;margin: auto;text-align: center;margin-top:0px;margin-bottom: 70px;font: 16px/30px "";border: 1px solid #ccc;border-radius: 5px;}
</style>