<template>
    <div class="Login">
        <header class="aui-header-default aui-header-fixed ">
			<router-link tag="a" class="aui-header-item" to="/home">
                <i class="aui-icon aui-icon-back"></i>
            </router-link>
        <div class="aui-header-center aui-header-center-clear">
			<div class="aui-header-center-logo">
				<div class="">登录</div>
			</div>
		</div>
		<a href="#" class="aui-header-item-icon"   style="min-width:0">
			<i class="aui-icon aui-icon-member"></i>
		</a>
	    </header>
	    <div style="height: 49px;"></div>
	    <!--<div class="login-logo">
	    	<img src="images/logo.png" />
	    </div>-->
	    <div style="height: 3rem;"></div>
        <div id="login-t">
            <input type="text" name="mobile"  id="login-mobile" placeholder="请输入用户名/手机号" class="login-name login-l" v-model="username">
            <input type="password" name="password"  id="login-password" placeholder="请输入密码" class="login-password login-l" v-model="password">
            <input type="button" class="login-btn login-l" value="我要登录" @click="login">
        </div>
	    <div class="login-agree">
	    	<router-link tag="a" to="/reg">免费注册</router-link>
        <div>{{mess}}</div>
	    	<a href="javascript:;" class="forget">忘记密码？</a>
	    </div>
	    <div class="login-line"></div>
	    <div class="login-line-text">第三方账号登录</div>
	    <ul class="login-second-login">
	    	<li><a href="javascript:;"><img src="../../assets/img/ad/wxlogin.png"/></a></li>
	    	<li><a href="javascript:;"><img src="../../assets/img/ad/qqlogin.png"/></a></li>
	    	<li><a href="javascript:;"><img src="../../assets/img/ad/sinalog.png"/></a></li>
	    </ul>
    </div>
</template>
<script>
export default {
  data(){
    return {
      detail:{},
      username:"",
      password:"",
      mess:"",
    }
  },
  methods:{
      login(){
        console.log(1,this.username,this.password)
        axios({
          url:"/api/login",
          withCredentials:true,
          method:"post",
          data:{username:this.username,password:this.password,save:true}
        }).then(
          res=>{
            if(res.data.err===0){
              this.$router.push("/uime")
              this.mess = res.data.mess
            }else{
              this.mess = res.data.mess
            }
          }
        )
      }
  },
}
</script>
<style>
    .login-l{display: block;border: 0;width: 80%;margin: 0 auto;font:14px/40px "";padding:3px 15px 3px 15px;border:1px solid #f2f2f2;border-radius: 5px;}
    #login-mobile{margin-top: 15px;}
    #login-password{margin-top: 15px;}
    .login-btn{margin-top: 25px;margin-bottom: 20px;background:red;color: #fff;}
    .login-agree{display:flex;justify-content:space-between;width: 80%;margin: 0 auto;margin-bottom: 20px;}
    .login-agree a{color: red;}
    .login-line{width: 80%;margin: 0 auto; border-bottom: 2px solid #f3f3f3;margin-bottom: 5px;}
    .login-line-text{width: 80%;margin: 0 auto;text-align: center;font: 20px/30px "";color: #333;}
    .login-second-login{width: 80%;margin: 0 auto;display: flex;justify-content:space-around;}
    .login-second-login li a img{width: 70%;margin-right: 0;}
</style>