<template>
    <div class="ProductsD">
        <section class="aui-new-search-content">
		<div class="aui-new-search-tab">
			<ul>
				<li>
					<span class="aui-active">综合</span>
				</li>
				<li>
					<span>销量</span>
				</li>
				<li>
					<span>价格</span>
				</li>
				<li>
					<span>筛选</span>
				</li>
			</ul>
		</div>
		<div class="aui-custom-search-tab">
			<ul>
				<li>
					<span class="aui-active">萌宝配送</span>
				</li>
				<li>
					<span>新品</span>
				</li>
				<li>
					<span>品牌</span>
				</li>
				<li>
					<span>前置摄像</span>
				</li>
			</ul>
		</div>
	</section>
	<section class="aui-list-product">
		<div class="aui-list-product-float-item">
			<div v-for="item of list" :key="item._id">
				<router-link tag="a" :to="`/uiproduct/${item._id}?dataName=jd`" class="aui-list-product-fl-item">
					<div class="aui-list-product-fl-img">
						<img  :src="baseUrl + '/upload' + item.img">
					</div>
					<div class="aui-list-product-fl-text">
						<h3 class="aui-list-product-fl-title">{{item.title}}</h3>
						<div class="aui-list-product-fl-mes">
							<div>
								<span class="aui-list-product-item-price">
									{{item.PresentP}}
								</span>
								<span class="aui-list-product-item-del-price">
									{{item.OriginalP}}
								</span>
							</div>
							<div class="aui-btn-purchase">
								99%好评
							</div>

						</div>
						<div class="aui-list-self-sell">
							<span class="aui-self-sell">自营</span>
							<em class="aui-self-sell-em">247800条评价</em>
						</div>

					</div>
				</router-link>
			</div>
		</div>
	</section>
    </div>
</template>
<script>
export default {
	data(){
		return{
		list:[],
		dataName:""
		}
	},
	mounted(){
		let dataName = this.$route.params.id;
		this.dataName = dataName;
		axios({
			url:`/api/${dataName}`
		}).then(
			res=>this.list = res.data.data
		)
  	}

}
</script>
